
import Swal from "sweetalert2";
import { fetchConToken } from "../helpers/fetch";
import { prepareTramites } from "../helpers/prepareTramites";
import { addTramite, Tramite, types } from "../types/types";

/*=============== ADD NEW TRAMITE ================== */
export const tramiteStartAddNew = (tramite:addTramite) => {
    return async(dispatch: (arg0: { type: types; payload: addTramite; }) => void, getState: () => { (): any; new(): any; auth: { uid: any; name: any; }; }) => {
        const {uid, name} = getState().auth
        tramite.user = {
            _id: uid,
            name:name
        }
        try {
            const resp = await fetchConToken('tramites', tramite, 'POST')
            const body = await resp.json()
            console.log(body)
            if(body.ok){
                tramite.id = body.tramite.id
                dispatch(tramiteAddNew(tramite))
                Swal.fire('Success', 'Tramite has been created successfully', 'success')
            }else{
                console.log(body.errors)
                if(body.errors){
                    Object.entries(body.errors).map(error => {
                        return error.map((err:any) => {
                            return Swal.fire('Error', err.msg, 'error')
                        })
                    })
                }
            }
        } catch (error) {
            console.log(error)
        }
    }
}

const tramiteAddNew = (tramite:addTramite) => ({
    type:types.tramiteAddNew,
    payload:tramite
})

/*=============== LOAD TRAMITES ================== */
export const tramiteStartLoading = () => {
    return async (dispatch: (arg0: { type: types; payload?: Tramite[]; }) => void) => {
        try {
            dispatch(tramiteLoading())
            const resp = await fetchConToken('tramites')
            const body = await resp.json()
            const tramites = prepareTramites(body.tramites)
            dispatch(tramitesLoaded(tramites))
        } catch (error) {
            console.log(error)
        }
    }
}

const tramitesLoaded = (tramites:Tramite[]) => ({
    type: types.tramiteLoaded,
    payload:tramites
})
/*=============== UPDATE TRAMITES ================== */
export const tramiteStartUpdate = (tramite:Tramite) => {
    return async(dispatch: (arg0: { type: types; payload: Tramite; }) => void) => {
        try {
            const resp = await fetchConToken(`tramites/${tramite.id}`, tramite, 'PUT')
            const body = await resp.json()

            if(body.ok) {
                dispatch(tramiteUpdate(tramite))
                Swal.fire('Success', 'Tramite has been updated successfully', 'success')
            }else{
                if(body.errors){
                    Object.entries(body.errors).map(error => {
                        return error.map((err:any) => {
                            return Swal.fire('Error', err.msg, 'error')
                        })
                    })
                }
            }
        } catch (error) {
            console.log(error)
        }
    }
}

const tramiteUpdate = (tramite:Tramite) => ({
    type:types.tramiteUpdate,
    payload:tramite
})

/*=============== DELETE TRAMITE ================== */
export const tramiteStartDelete = (id:number) => {
    return async(dispatch: (arg0: { type: types; payload: number; }) => void) => {
        try {
            const resp = await fetchConToken(`tramites/${id}`, {}, 'DELETE')
            const body = await resp.json()

            if(body.ok) {
                dispatch(tramiteDelete(id))
                Swal.fire('Success', 'Tramite deleted successfully')
            }else{
                if(body.errors){
                    Object.entries(body.errors).map(error => {
                        return error.map((err:any) => {
                            return Swal.fire('Error', err.msg, 'error')
                        })
                    })
                }
            }
        } catch (error) {
            console.log(error);
        }
    }
}

const tramiteDelete = (id:number) => ({
    type:types.tramiteDeleted,
    payload:id
}) 


